import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import {MatFormFieldModule} from '@angular/material/form-field';


import { EmployeeService } from './employees.service';
import { Employee } from './employee';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-employees',
  templateUrl: './employees.component.html',
  styleUrls: ['./employees.component.css'],
  // encapsulation: ViewEncapsulation.None
})
export class EmployeesComponent implements OnInit {
  // Component properties
  employees: Employee[];
  statusCode: number;
  requestProcessing = false;
  employeeIdToUpdate = null;
  processValidation = false;
  // Create form
  employeeForm = new FormGroup({
    name: new FormControl('', Validators.required),
    email: new FormControl('', Validators.required),
    designation: new FormControl('', Validators.required)
  });
  // Create constructor to get service instance
  constructor(private employeeService: EmployeeService) {
  }
  // Create ngOnInit() and and get employees
  ngOnInit(): void {
    this.getAllEmployees();
  }
  // get all employees
  getAllEmployees() {
    this.employeeService.getAllEmployees()
      .subscribe(
        (data) => {
          this.employees = data;
          console.log('data is ', data);
        } ,
        errorCode =>  this.statusCode = errorCode);
  }
  // Handle create and update employee
  onEmployeeFormSubmit() {
    this.processValidation = true;
    if (this.employeeForm.invalid) {
      return; // Validation failed, exit from method.
    }
    // Form is valid, now perform create or update
    this.preProcessConfigurations();
    const employee = this.employeeForm.value;
    if (this.employeeIdToUpdate === null) {

      // Create employee
      this.employeeService.addEmployee(employee)
        .subscribe(response => {
          console.log('succescode', response);
          console.log();
            this.statusCode = response.status;
             this.getAllEmployees();
             this.backToCreateEmployee();
          },
          error => this.statusCode = error.status
        );

    } else {
      // Handle update employee
      employee._id = this.employeeIdToUpdate;
      console.log("in update 1");
      this.employeeService.updateEmployee(employee)
        .subscribe(response => {
            console.log("in update 2", response);
            this.statusCode = response.status;
            this.getAllEmployees();
            this.backToCreateEmployee();
          },
          error => {
          console.log("eeeeeeeee");
          this.statusCode = error.status;
        });
    }
  }
  // Load employee by id to edit
  loadEmployeeToEdit(employeeId: string) {
    console.log("in preupdate 1");
    this.preProcessConfigurations();
    this.employeeService.getEmployeeById(employeeId)
      .subscribe(employee => {
          console.log("in preupdate 2");
          this.employeeIdToUpdate = employee._id;
          this.employeeForm.setValue({ name: employee.name, email: employee.email, designation: employee.designation });
          this.processValidation = true;
          this.requestProcessing = false;
        },
        error =>  this.statusCode = error.status);
  }
  // Delete employee
  deleteEmployee(employeeId: string) {
    this.preProcessConfigurations();
    this.employeeService.deleteEmployeeById(employeeId)
      .subscribe(response => {
        console.log("del respn", response);
          // this.statusCode = successCode;
          // Expecting success code 204 from server
          this.getAllEmployees();
          this.backToCreateEmployee();
          this.statusCode = response.status;
        },
        error => this.statusCode = error.status);
  }
  // Perform preliminary processing configurations
  preProcessConfigurations() {
    this.statusCode = null;
    this.requestProcessing = true;
  }
  // Go back from update to create
  backToCreateEmployee() {
    this.employeeIdToUpdate = null;
    this.employeeForm.reset();
    this.processValidation = false;
  }
}



