import { Injectable } from '@angular/core';



import { Employee } from './employee';
import {HttpClient, HttpResponse} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';

@Injectable()
export class EmployeeService {
  // URL for CRUD operations
  employeeAPI = '/api/employees';
  // Create constructor to get Http instance
  constructor(private httpClient: HttpClient) {
  }
  // get all employees
  getAllEmployees(): Observable<Employee[]> {
    return this.httpClient.get<Employee[]>(this.employeeAPI);
  }
  // add employee
  addEmployee(employee: Employee): Observable<HttpResponse<Employee>> {
    return this.httpClient.post<Employee>(this.employeeAPI, employee, {observe: 'response'});
  }
  // // Fetch employee by id
  getEmployeeById(employeeId: string): Observable<Employee> {
    return this.httpClient.get<Employee>(this.employeeAPI + '/' + employeeId);
  }
  // Update employee
  updateEmployee(employee: Employee): Observable<HttpResponse<Employee>> {
    return this.httpClient.put<Employee>(this.employeeAPI, employee, {observe: 'response'});
  }
  // // Delete employee
  deleteEmployeeById(employeeId: string): Observable<HttpResponse<Object>> {
    return this.httpClient.delete(this.employeeAPI + '/' + employeeId, {observe: 'response'});
  }
}



